* {
  padding: 0;
  margin: 0;
  border: 0;
}

@media screen and (max-width: 456px) {
    .home{
    
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    
    }
}
    

body {
    background-color: #fffdf8;
    font-family: 'Raleway', sans-serif;
    /* padding: 0 !important;
    margin: 0 !important;
    border: 0 !important; */
}


h2 {
    font-family: 'Lobster', cursive;
    /*font-weight: bold;*/
    text-align: center; 
    margin-top: 80px;
    margin-bottom: 60px;
}
h3 {
    font-size: 1.1em;
    padding-top: 20px;
    margin-bottom: 0;
}
ol {
    text-align: left; 
}
a:hover, a:focus, a:visited {
    text-decoration: none;
}
/*** MENU ***/
.navbar-brand {
    margin-top: -22px;
}
.navbar-default {
    border-radius: 0px;
    border-bottom: 2px, #fffdf8;
    border-top: none;
    border-right: none;
    border-left: none;
    background-color: transparent;
    padding: 10px;
    margin-bottom: 0px;
    font-family: 'Lobster', cursive;
    font-size: 1.7em;
}
.navbar-default .navbar-nav li a {
    color: #fffdf8;
}
.navbar-default .navbar-nav li a:hover, 
.navbar-default .navbar-nav li a:focus {
    color: orange;
}
.navbar-default .navbar-nav > .active > a, 
.navbar-default .navbar-nav > .active > a:hover, 
.navbar-default .navbar-nav > .active > a:focus {
    color: orange;
    background-color: transparent;
}
.navbar-default .navbar-toggle .icon-bar {
  background-color: orange; /* Changes regular toggle color */
}
/* navbar change on scroll */
.logo-blanc {
    position: absolute;
    z-index: 1;
}
.home-btn {
    position: absolute;
    z-index: 2;
    margin-top: -30;
    height: 60px;
}
.affix {
  background-color: #fffdf8;
  padding:0px;         
  transition:padding 0.2s linear;  
}
.affix .navbar-nav li a {
    color: #222222;
    font-size: 0.9em;
}
.affix .logo-blanc {
    visibility: hidden;
}
.affix-top {
  padding-top:15px;
  padding-bottom:15px;         
  transition:padding 0.5s linear;  
}
.affix-top .home-btn {
    visibility: hidden;
}
header .social-network {
    font-size: 0.6em;  
}
.social-network .fa {
    color: #4f929f;
}
/************* HEADER *************/
.image-bg1 { 
 background: url(/static/media/codigo-programacion-portatil-fondo.426e138e.jpg) no-repeat center center scroll;
    background-attachment: fixed;
    background-size: cover;
    -o-background-size: cover;  
    min-height: 878px;
}
h1{
    font-family: 'Lobster', cursive;
    color: #fffdf8;
    text-align: center;
    margin-top: 250px;
    font-size: 6em;
    
}
 .sous-titre{
    font-size: 2.5em;
    color: #fffdf8;
    text-align: center;
    font-family: 'Raleway', sans-serif;
    margin-top: -30px;
}

@media (max-width: 768px) {
    .navbar-nav{
        margin-right: 110px;
    }
    .social-network{
        display: none;
    }
}

@media only screen and (min-width: 360px) and (max-width: 768px) {
    .navbar-nav{
        margin-right: 180px;
    }
}



/************* SEPARATOR *************/
.separator {
    background-color: #4f929f;
    height: 30px;
}
/************* PARCOURS *************/
.carousel-control.right, .carousel-control.left {
  background-image:none;
    color: orange;
}
.carousel {
    margin: 50px;
}
.date {
    font-weight: bold;
    font-size: 0.9em;
}
.cv {
    margin-bottom: 50px;
    background-color: #fffdf8;
    min-height: 780px;
    
}
.cv h3 {
    text-align: center; 
    font-weight: bold;
    font-size: 0.9em;
    margin-top: -35px;
    padding: 50px;
} 
.cv .btn {
    margin-top: 20px;
}

/************* ABOUT *************/
.about {
    font-family: 'Raleway', sans-serif;
    font-size: 1.2em;
    color: #fffdf8;
    background: rgb(255,255,255);
    background: linear-gradient(45deg, #7bcfe0 0%, #4f929f 60%);
}
.about p {
    margin-bottom: 80px;
    text-align: center; 
}
/************* PORTFOLIO *************/

.folio, .skills, .cv {
    font-family: 'Raleway', sans-serif;
    color: #222222;
    background-color: #fffdf8;
    text-align: center;
    padding-bottom: 80px;
    
}

.thumbnail img:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    -webkit-filter: grayscale(50%); /* Safari 6.0 - 9.0 */
    filter: grayscale(50%);
}
.thumbnail img {
    transition: all 0.3s ease 0s;
    width: 100%;
    -webkit-filter: grayscale(100%); /* Safari 6.0 - 9.0 */
    filter: grayscale(100%);
    padding: 5px;
    
}
.thumbnail {
    overflow: hidden;
    border: none;
    box-shadow: 1px 1px 5px #222222; 
    background-color: #eddbc3;
}

.frame {
    background-color: #4f929f;
    padding-top: 15px;
    
}
.frame h3 {
    background-color: #4f929f;
    color: #eddbc3;
    margin-top: -15px;
    padding-bottom: 15px;
    font-weight: bold;
    text-align: center; 
}
.soon img:hover{
    -webkit-transform: none;
            transform: none;
    -webkit-filter: grayscale(0%); /* Safari 6.0 - 9.0 */
    filter: none;  
}
.soon img{
    -webkit-filter: grayscale(0%); /* Safari 6.0 - 9.0 */
    filter: none;  
}
.soon h3{
    color: transparent;
}
/************* COMPETENCES *************/

#skills-container{
    display: grid;
    grid-template-columns: repeat(auto-fill, 230px);
    align-items: center;
    justify-content: center;

}

.skills h3 {
    text-align: center; 
    font-weight: bold;
    font-size: 0.9em;
    margin-top: -35px;
    padding: 50px;
} 

.btn-warning{
    background-color: #4f929f;
    border: none;
    box-shadow: #000000 1px 1px 5px;
}

.btn-warning:hover{
    background-color: #43b1c7;
    border: none;
    box-shadow: #000000 2px 2px 5px;
}
.modal-body {
    color: black;
}

.btn-secondary {
    color: black;
    background-color: rgba(216, 61, 61, 0.918);
    box-shadow: -5px -5px 9px rgb(255, 255, 255), 5px 5px 9px rgba(7, 7, 7, 0.726);
}

/************* CONTACT *************/

footer {
    font-family: 'Raleway', sans-serif;
    color: #fffdf8;
    text-align: center; 
    background-color: transparent;
    min-height: 500px;
    /* background: url('../../image/footer-1.jpg') no-repeat center center scroll; */
    background-attachment: fixed;
    background-size: cover;
    -o-background-size: cover;  
    /* background-color: #4f929f; */
    background: rgb(255,255,255);
    background: linear-gradient(45deg, #7bcfe0 0%, #4f929f 60%);
    
}
footer img {
    border: 6px;
    border-style: solid;
    border-color: #4f929f;
    border-radius: 100%;
    /*opacity: 0.6;
    filter: alpha(opacity=60); /* For IE8 and earlier */
}
footer .conclusion {
    padding: 0px 0px 40px 0px;
}
/* Forms---------------------------------- */
.form-control {
	height: 35px;
	border-radius: 5px;
    background-color: rgba(255, 243, 248, 0.6);
}
.form-control-feedback {
	color: #4f929f;
}
.has-feedback label.sr-only ~ .form-control-feedback {
	top: 10px;
}
textarea {
	resize: vertical;
}
form .btn {
    min-width: 30%;
    color: #4f929f;
    background-color: #fffdf8;
    margin-bottom: 50px;
}
form .btn:hover {
    color: orange;
}
/* Fin forms---------------------------------- */
p .fa-envelope {
    padding-top: 30px;
}
.email {
    font-size: 1.5em;
}
.social-network i {
    background-color: #fffdf8;
    border-radius: 100%;
    color: #4f929f;
    padding: 5px 6px 5px 6px;
    margin: 13px 0px 0px 5px;
}
.social-network .fa-facebook {
    padding: 5px 8px 5px 8px;
}
.social-network i:hover {
    color: orange;
}
.cr {
    font-size: 0.8em;
}
